const EMPTY_DISK = "";

const createEmptyDisk = (index) => {
    const binaryString = atob(EMPTY_DISK);
    const bytes = new Uint8Array(binaryString.length);
    for (let i = 0; i < binaryString.length; i++) {
        bytes[i] = binaryString.charCodeAt(i);
    }
    bytes[0x1659A] = 48 + index;
    return bytes;
}

export { createEmptyDisk }